import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import axios from 'axios';
import { createBrowserHistory } from 'history';
import User from './User';
class Dob extends Component {

	constructor(props) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this)
    this.onChangep_no = this.onChangep_no.bind(this)
    this.onChangem_no = this.onChangem_no.bind(this)
     this.state = {
            p_no: "",
            m_no:""
        };
   
  }


  state = {
    redirect: false
  }
  setRedirect = () => {
    this.setState({
      redirect: true
    })
  }
  renderRedirect = () => {
    if (this.state.redirect) {
     
      let todo = {
            p_no: this.state.p_no,
            m_no: this.state.m_no
        }
        axios.post("http://localhost:8001/todos/PolicyList", todo).then(res => {
            console.log(res.data);
            this.props.history.push({pathname:"PolicyList",
              state:{
                p_no:res.data.p_no,
                m_no:res.data.m_no
              }});
            });
        

    
  }
}

 onChangep_no = event => {
        this.setState({
            p_no:event.target.value,
        })
    }
    
 onChangem_no = event => {
        this.setState({
            m_no:event.target.value,
        })
    } 

handleSubmit(event) {
      const len=this.state.p_no.length
        if(len!=9)
        alert("Invalid Length");
      else
      {
        event.preventDefault()

        let User = {
            m_no: this.state.m_no,
            p_no: this.state.p_no,
        }
        let stringify = require('json-stringify-safe');
        axios.post("http://localhost:8001/todos/dob", User)
        .then((res) => res.data)
    .then((res) => {
    
    if(res === "Successful")
    {
       this.setRedirect();
       this.renderRedirect();
    }
    else
    {
        alert("Invalid Credentials");
    }
  })

        
    }
  }


  render() {
    return (
   <center>
    	<form class="align">
  <div class="form-group row">
    
    <div class="col-sm-10">
      <input type="text" class="form-control bod" name="p_no" id="inputPolicy" placeholder="Enter your Policy No." value={this.state.p_no}
                                onChange={this.onChangep_no}/>
    </div>
  </div>
  
  <button type="button" class="btn btn-secondary" onClick={this.handleSubmit}>Submit</button>
</form>
   </center> 
  );
     
    
  }
}

export default Dob;




























