import axios from 'axios';
import User from './User';
import React, { Component } from 'react';
import DropdownButton from 'react-bootstrap/DropdownButton';
import Dropdown from 'react-bootstrap/Dropdown';
  class PolicyList extends Component {
  
    constructor(props) {
    super(props);
      this.state = {
        selectedFile1: null,
        selectedFile2:null
      }
   
  }
  onChangeHandler1=event=>{
    this.setState({
      selectedFile1: event.target.files[0],
      loaded: 0,
    })
     
    
  }
  onChangeHandler2=event=>{
    this.setState({
      selectedFile2: event.target.files[0],
      loaded: 0,
    })
     
    
  }
   onClickHandler2=event=>{
    
     let data = new FormData();
   data.append('file',this.state.selectedFile2);
   data.append('p_no',this.props.location.state.p_no);
   console.log("checking data",data);
   axios.post("http://localhost:8001/todos/PolicyList/docs2", data)

   .then(res => { 
    console.log(res.statusText)
 })
    
  }
   

    onClickHandler1 = () => {
   
   let data = new FormData();
   data.append('file',this.state.selectedFile1);
   data.append('p_no',this.props.location.state.p_no);
   console.log("checking data",data);
   axios.post("http://localhost:8001/todos/PolicyList/docs", data)

   .then(res => { 
    console.log(res.statusText)
 })
}


 

    render() {

     
        return (
            <div className="details"><table className="table dis">
  <thead class="heading">
    <tr>
      <th scope="col">Policy</th>
      <th scope="col">Phone</th>
    </tr>
  </thead>
  <tbody>
    <tr>
      <td>{ this.props.location.state.p_no}</td>
      <td>{ this.props.location.state.m_no}</td>
   
</tr>


<DropdownButton id="dropdown-item-button" title="Documents">
  <Dropdown.Item as="button"><b>Aadahar:</b><input type="file" name="file" onChange={this.onChangeHandler1} /><button onClick={this.onClickHandler1}>Submit</button></Dropdown.Item>
  <Dropdown.Item as="button"><b>Pan card:</b><input type="file" name="file" onChange={this.onChangeHandler2}/><button onClick={this.onClickHandler2}>Submit</button></Dropdown.Item>
</DropdownButton>


</tbody>
</table>
  
</div>
        );
    }
}

export default PolicyList;