const mongoose = require('mongoose');
const Schema = mongoose.Schema;

let User = new Schema({
    p_no :{
        type: String
    },
    m_no :{
        type: String
    },
    aadhar:{
    	type: String
    	    },
    pan:{
    	type: String
    	 }
    });

module.exports = mongoose.model('User', User,'users');